import React, { useRef, useEffect, useState } from "react";
import { Link, animateScroll as scroll } from "react-scroll";
import { Button, Nav, Navbar, Container, Offcanvas } from "react-bootstrap/";

import "../Nav.css";

function Navigation() {

  const [show, setShow] = useState(false);
  const [isSmallScreen, setIsSmallScreen] = useState(window.innerWidth < 992);
 
  useEffect(() => {
    const checkScreenSize = () => {
      setIsSmallScreen(window.innerWidth < 992);
    };

    window.addEventListener("resize", checkScreenSize);

    return () => window.removeEventListener("resize", checkScreenSize);
  }, []);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const navbarRef = useRef(null);
  const [navbarHeight, setNavbarHeight] = useState(0);

  useEffect(() => {
    const handleResize = () => {
      setNavbarHeight(navbarRef.current?.offsetHeight || 0);
    };
    console.log(navbarHeight);

    handleResize(); // call this once to ensure height is updated on initial render

    // add event listener for subsequent resizes
    window.addEventListener("resize", handleResize);

    // cleanup function
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []); // empty dependency array means this runs on mount and unmount
  

  return (
    <Container fluid>
      <Navbar id="content-top" style={{ position: "relative"}}></Navbar>

      <Navbar
        ref={navbarRef}
        fixed="top"
        id="nav-mod"
        expand="lg"
        
        variant="dark"
      >
        <Navbar.Brand>
          <Link
            activeClass="active"
            to="content-top"
            spy={true}
            smooth={true}
            offset={-navbarHeight - 300}
            duration={500}
            className="mx-lg-3 mx-3 nav-link"
          >
            Proyecto Caneln
          </Link>
        </Navbar.Brand>

        {isSmallScreen ? (
          <>
        
             <Navbar.Toggle onClick={handleShow} aria-controls="basic-navbar-nav" className="nav-collapse border-0" />
            <Offcanvas className='text-bg-dark' show={show} onHide={handleClose} placement="start">
              <Offcanvas.Header closeButton closeVariant="white">
                <Offcanvas.Title>Proyecto Caneln</Offcanvas.Title>
              </Offcanvas.Header>
              <Offcanvas.Body>
                <Nav
                  variant="tabs"
                  defaultActiveKey="intro"
                  className="mr-auto flex-column"
                >
                  <Nav.Item>
                    <Link
                      activeClass="active"
                      to="intro"
                      spy={true}
                      smooth={true}
                      offset={-navbarHeight - 20}
                      duration={500}
                      className="nav-link"
                    >
                      Quienes somos
                    </Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Link
                      activeClass="active"
                      to="map"
                      spy={true}
                      smooth={true}
                      offset={-navbarHeight - 25}
                      duration={500}
                      className="nav-link"
                    >
                      Plantaciones
                    </Link>
                  </Nav.Item>

                  <Nav.Item>
                    <Link
                      activeClass="active"
                      to="especies"
                      spy={true}
                      smooth={true}
                      offset={-navbarHeight - 30}
                      duration={500}
                      className="nav-link"
                    >
                      Arboles
                    </Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Link
                      activeClass="active"
                      to="unete"
                      spy={true}
                      smooth={true}
                      offset={-navbarHeight - 40}
                      duration={500}
                      className="nav-link"
                    >
                      Unete
                    </Link>
                  </Nav.Item>
                </Nav>
              </Offcanvas.Body>
            </Offcanvas>
          </>
        ) : (
          <Navbar.Collapse
            className="justify-content-end mx-3 nav-collapse"
            id="basic-navbar-nav"
          >
            <Nav variant="tabs" defaultActiveKey="intro" className="mr-auto">
              <Nav.Item>
                <Link
                  activeClass="active"
                  to="intro"
                  spy={true}
                  smooth={true}
                  offset={-navbarHeight - 20}
                  duration={500}
                  className="nav-link"
                >
                  Quienes somos
                </Link>
              </Nav.Item>
              <Nav.Item>
                <Link
                  activeClass="active"
                  to="map"
                  spy={true}
                  smooth={true}
                  offset={-navbarHeight - 25}
                  duration={500}
                  className="nav-link"
                >
                  Plantaciones
                </Link>
              </Nav.Item>

              <Nav.Item>
                <Link
                  activeClass="active"
                  to="especies"
                  spy={true}
                  smooth={true}
                  offset={-navbarHeight - 30}
                  duration={500}
                  className="nav-link"
                >
                  Arboles
                </Link>
              </Nav.Item>
              <Nav.Item>
                <Link
                  activeClass="active"
                  to="unete"
                  spy={true}
                  smooth={true}
                  offset={-navbarHeight - 40}
                  duration={500}
                  className="nav-link"
                >
                  Unete
                </Link>
              </Nav.Item>
            </Nav>
          </Navbar.Collapse>
        )}
      </Navbar>
    </Container>
  );
}

export default Navigation;
