import React, { useEffect, useState } from "react";
import {
  GoogleMap,
  LoadScript,
  MarkerF,
  InfoWindowF,
} from "@react-google-maps/api";

const containerStyle = {
  width: "75%",
  height: "500px",
};

const center = {
  lat: -34.8167,
  lng: -55.95,
};

const mapStyles = [
  {
    featureType: "poi",
    stylers: [{ visibility: "off" }],
  },
  {
    featureType: "transit",
    stylers: [{ visibility: "off" }],
  },
  {
    featureType: "road",
    elementType: "labels.icon",
    stylers: [{ visibility: "off" }],
  },
];

function Map() {
  const [map, setMap] = useState(null);
  const [selectedTree, setSelectedTree] = useState(null);
  const [treeData, setTreeData] = useState([]);

  useEffect(() => {
    fetch("trees.json")
      .then((response) => response.json())
      .then((data) => setTreeData(data))
      .catch((error) => console.error("Error:", error));
  }, []);

  return (
    
    <div id='map' className="container">
      <div>
        <h2 className="mt-3 mb-3 row justify-content-center titulo display-5">
          {" "}
          Mapeo de Plantaciones{" "}
        </h2>
      </div>
      <div className="row justify-content-center">
        <LoadScript googleMapsApiKey="AIzaSyBScJtgAN3NEEhEWqH4aNPFJHxXst4cIcY">
          <GoogleMap
            mapContainerStyle={containerStyle}
            center={center}
            zoom={13}
            options={{ styles: mapStyles, minZoom: 10 }}
            onLoad={setMap}
            onUnmount={() => setMap(null)}
          >
            {/* Child components, such as markers, info windows, etc. */}

            {Object.entries(treeData).map(([key, tree]) => (
              <MarkerF
                key={key}
                position={{
                  lat: parseFloat(tree.coordenadas.latitud),
                  lng: parseFloat(tree.coordenadas.longitud),
                }}
                title={tree.lugar}
                onClick={() => setSelectedTree(tree)}
              />
            ))}
            {selectedTree && (
              <InfoWindowF
                position={{
                  lat: parseFloat(selectedTree.coordenadas.latitud),
                  lng: parseFloat(selectedTree.coordenadas.longitud),
                }}
                onCloseClick={() => setSelectedTree(null)}
              >
                <div>
                  <h4>{selectedTree.lugar}</h4>
                  <h5> Fecha </h5>
                  <p>{selectedTree.fecha}</p>
                  <h5> Especies</h5>
                  {Object.entries(selectedTree.especies).map(([key, value]) => (
                    <p key={key}>
                      {key}: {value}
                    </p>
                  ))}
                </div>
              </InfoWindowF>
            )}
          </GoogleMap>
        </LoadScript>
      </div>
    </div>
  );
}

export default React.memo(Map);
