import React, { useState } from "react";
import { Carousel, Row, Col, Container } from "react-bootstrap";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
/* import treeData from "../assets/data_tree.json"; */
import TreeModal from "./Tree";
import "../TreeSection.css";

const TreeSection = () => {
  const treeImagesContext = require.context(
    "../assets/arboles",
    false,
    /\.jpg$/
  );

  // This array will contain an object for each image, with the image URL and filename
  const treeImages = treeImagesContext.keys().map((filename) => {
    return {
      name: filename.substr(2).replace(".jpg", ""), // strip leading "./" and trailing ".png"
      img: treeImagesContext(filename),
    };
  });

  // Now you can map over treeImages in your render method, just like in the previous example

  const [selectedTree, setSelectedTree] = useState(null);
  const [index, setIndex] = useState(0);

  const handleSelect = (selectedIndex, e) => {
    setIndex(selectedIndex);
  };

  /*  const handleShowModal = (tree) => {
    setSelectedTree(tree);
  };

  const handleCloseModal = () => {
    setSelectedTree(null);
  };
 */
  return (
    <Container id="especies">
      <h2 className="mt-5 row justify-content-center titulo display-5"> Arboles </h2>
      <Carousel
        className="my-carousel mt-2"
        activeIndex={index}
        onSelect={handleSelect}
        variant="dark"
      >
        {treeImages.map((tree, i) => (
          <Carousel.Item key={i}>
            <Row>
              <Col>
                <img className="imagenes" src={tree.img} alt={tree.name} />
                {/* <Carousel.Caption>
                  <h3>{tree.name}</h3>
                </Carousel.Caption> */}
              </Col>
            </Row>
          </Carousel.Item>
        ))}
      </Carousel>
    </Container>
  );
};

export default TreeSection;
