import React, { useState } from 'react';
import Modal from 'react-bootstrap/Modal';

function TreeModal({ tree }) {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <img src={tree.image} alt={tree.tree_species} onClick={handleShow} />

      <Modal show={show} onHide={handleClose} size="lg">
        <Modal.Header closeButton>
          <Modal.Title>{tree.tree_species}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <img src={tree.image} alt={tree.tree_species} className="img-fluid" />
          <p>Height: {tree.tree_height}</p>
          <p>Age: {tree.tree_age}</p>
          <p>Location: {tree.tree_location}</p>
          <p>Planted Date: {tree.tree_planted_date}</p>
        </Modal.Body>
      </Modal>
    </>
  );
}

export default TreeModal;

